import { Component, OnInit } from '@angular/core';

import { BannerService } from '../../angular-services/banner.service';
import { Banner } from '../../classes/banner';

@Component({
  selector: 'app-list-banners-admin',
  templateUrl: './list-banners.component.html',
  styleUrls: ['./list-banners.component.css']
})
export class ListBannersComponent implements OnInit {

  banners: Banner[] = [];
  alert: any = { type: '', message: '', show: false };

  constructor(private bannerService: BannerService) { }

  ngOnInit() {
    this.getBanners();
  }

  getBanners() {
    this.bannerService.getBanners().subscribe(
      response => {
        if (response !== undefined) {
          this.banners = response;
        } else {
          alert('Houve algum erro ao carregar os dados!');
        }
      }
    )
  }

  removeBanner(banner) {
    let confirm = prompt('Para confirmar esta operação digite SIM: \n\n ');
    if (confirm === 'SIM') {
      this.banners = this.banners.filter(b => b !== banner);
      this.bannerService.removeBanner(banner._id).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Banner excluído com sucesso!';
            this.alert.show = true;
          } else {
            this.alert.type = 'danger';
            this.alert.message = 'Houve algum erro ao carregar os dados!';
            this.alert.show = true;
          }
        }
      )
    } else {
      this.alert.type = 'danger';
      this.alert.message = 'O captcha inválido!';
      this.alert.show = true;
    }
  }

}
